/* src/App.css */

html {
  scroll-behavior: smooth;
}

/* Body */
.App {
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1200px; 
  margin: 0 auto; 
  background-color: #034951;
}

body {
  background-color: #003135;
}

@media (max-width: 768px) {
  .App {
    padding-left: 10px;
    padding-right: 10px;
  }
}

/* About */

.about {
  padding-top: 2rem;
  padding-left: 20px;
  padding-right: 20px;

  color: #AEDDE5;
}

.about-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between; 
  gap: 20px;
  padding: 20px;
}

.about-image img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  width: 300px; 
}

.about-text {
  padding-left: 1rem;
  flex: 1; 
}

.about-text h2 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.about-text p {
  font-size: 1.1rem;
  line-height: 1.6;
}

/* Projects */
.projects-header {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #AEDDE5;
}

.project-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}

.projects-header {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: #AEDDE5;
}

.project-list {
  display: flex;
  flex-direction: column;
  gap: 30px; 
  padding: 20px;
}

.project-item {
  display: flex;
  align-items: flex-start;
  background-color: #AEDDE5;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.project-image {
  width: 150px; 
  height: auto;
  margin-right: 20px;
  border-radius: 10px;
}

.project-content {
  flex: 1;
}

.project-content h3 {
  margin-bottom: 10px;
  font-size: 1.5rem;
  color: #444;
}

.project-content p {
  font-size: 1rem;
  line-height: 1.5;
  color: #666;
}

.project-content button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  cursor: pointer;
  margin: 10px 0;
}

.project-content button:hover {
  background-color: #0056b3;
}

.project-link {
  display: block;
  margin-top: 10px;
  color: #007bff;
  text-decoration: none;
}

.project-link:hover {
  text-decoration: underline;
}


/* Contact */
.contact-details {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.contact-item {
  font-size: 1.2rem; 
  color: #AEDDE5; 
  margin: 10px 0; 
}

.contact-item a {
  color: #007bff; 
  text-decoration: none;
}

.contact-item a:hover {
  text-decoration: underline; 
}


/* Navigation bar */
.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #AEDDE5;
}

.nav-logo h1 {
  margin: 0;
  font-size: 3rem;
  font-weight: bold;
}

.nav-pages ul, .nav-links ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav-pages ul li, .nav-links ul li {
  margin: 0 10px;
}

.nav-pages ul li a, .nav-links ul li a {
  color: #AEDDE5;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s;
}

.nav-pages ul li a:hover, .nav-links ul li a:hover {
  color: #964834;
}

@media (max-width: 768px) {
  .nav-pages ul, .nav-links ul {
    flex-direction: column;
  }
}
